<template>
  <v-container @contextmenu="show">
    <v-row>

      <v-col cols="12">
        <v-row align="start" justify="start" class="grey lighten-5">
          <v-toolbar dense flat>
            <!-- <v-app-bar-nav-icon></v-app-bar-nav-icon> -->
            <v-menu v-model="menu" :close-on-content-click="true" offset-x>
              <template v-slot:activator="{ on, attrs }">
                <v-btn color="black" text outlined v-bind="attrs" v-on="on">
                  Home
                  <v-icon right dark>mdi-chevron-down</v-icon>
                </v-btn>
              </template>
              <v-card>
                <v-list dense class="py-0">
                  <v-list-item-group color="primary">
                    <v-list-item class="pa-0 my-0">
                      <v-btn text block class="mx-0 pa-6 rounded-lg grey lighten-3 outlined"
                        style="text-transform: none;">
                        <v-icon left>mdi-folder</v-icon>
                        New Folder
                      </v-btn>
                    </v-list-item>
                    <v-divider class="py-0 my-0"></v-divider>
                  </v-list-item-group>
                  <v-list-item-group color="primary">
                    <v-list-item v-for="(action, i) in actions" :key="i" class="pa-0 my-0">
                      <file-upload
                        class=" v-btn v-btn--flat v-btn--text theme--light v-size--default ma-0 pa-2 rounded-lg block outlined"
                        style="text-transform: none;" post-action="http://127.0.0.1:5000/uploadtest" :multiple="true"
                        :drop="true" :data="data" :directory="true" :drop-directory="true" v-model="files"
                        @input-file="inputFile" ref="upload">
                        <v-icon right>mdi-folder-plus</v-icon>
                        Upload Files
                      </file-upload>
                    </v-list-item>

                  </v-list-item-group>
                </v-list>
              </v-card>
            </v-menu>
            <v-spacer></v-spacer>
            <v-btn icon>
              <v-icon>mdi-view-list</v-icon>
            </v-btn>
            <v-btn icon>
              <v-icon>mdi-information</v-icon>
            </v-btn>
          </v-toolbar>
        </v-row>
      </v-col>

      <v-col cols="12">
        <v-row align="start" justify="start">
          <v-col cols="12" class="pa-0">
            <v-subheader>Folders</v-subheader>
          </v-col>
          <v-col cols="12" class="pa-0">
            <v-btn v-for="(item,i) in [1,2,3,4]" :key="i" text class="ma-2 pa-6 rounded-lg grey lighten-3 outlined">
              <v-icon left>mdi-folder</v-icon>
              New Folder {{ item }}
            </v-btn>
          </v-col>

        </v-row>
      </v-col>

      <v-col cols="12">
        <v-row align="start" justify="start">
          <v-col cols="12" class="pa-0">
            <v-subheader>Files</v-subheader>
          </v-col>

          <v-col cols="12" class="pa-0">
            <v-simple-table fixed-header height="300px">
              <template v-slot:default>
                <thead>
                  <tr>
                    <th class="text-left">Name</th>
                    <th class="text-left">Last Modified</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="item in desserts" :key="item.name">
                    <td>
                      <v-icon class="px-2">mdi-file</v-icon> {{ item.name }}
                    </td>
                    <td>{{ item.calories }}</td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
          </v-col>

        </v-row>
      </v-col>
    </v-row>
    <v-menu v-model="showMenu" :position-x="x" :position-y="y" absolute offset-y>
      <v-card class="mx-auto" max-width="300" tile>
        <v-list dense class="py-0">
          <v-list-item-group color="primary">
            <v-list-item>
              <v-list-item-icon>
                <v-icon>mdi-folder-plus</v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title>New Folder</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
            <v-divider class="py-1 my-0"></v-divider>

          </v-list-item-group>
          <v-list-item-group color="primary">
            <v-list-item v-for="(action, i) in actions" :key="i">
              <v-list-item-icon>
                <v-icon v-text="action.icon"></v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title v-text="action.text"></v-list-item-title>
              </v-list-item-content>
              <!-- <v-divider></v-divider> -->
            </v-list-item>

          </v-list-item-group>
        </v-list>
      </v-card>
    </v-menu>
  </v-container>


</template>

<script>
  import {
    mapGetters
  } from "vuex";
  // import { mapState } from "vuex";
  // import { mapWaitingActions, mapWaitingGetters } from "vue-wait";
  import {
    mapWaitingActions
  } from "vue-wait";


  export default {
    computed: {
      ...mapGetters([
        "LoadFolders",
        // ...
      ])
    },
    watch: {
      LoadFolders: function (o) {
        if (o != null) {
          console.log(o);
        }
      }
    },

    data: () => ({
      menu: false,
      showMenu: false,
      x: 0,
      y: 0,
      files: [],
      data: {
        'directory': 'home/',
      },
      desserts: [{
          name: 'Frozen Yogurt.xslx',
          calories: "Dec, 2020",
        },
        {
          name: 'Ice cream sandwich.pdf',
          calories: "Dec, 2020",
        },
        {
          name: 'Eclair.csv',
          calories: "Dec, 2020",
        },

      ],
      actions: [{
          text: "Upload Files",
          icon: "mdi-file-multiple"
        },
        {
          text: "Upload Folder",
          icon: "mdi-folder-open"
        }
      ]
    }),
    methods: {
      ...mapWaitingActions({
        insertNewFolder: "addFolder",
      }),
      show(e) {
        e.preventDefault();
        this.showMenu = false;
        this.x = e.clientX;
        this.y = e.clientY;
        this.$nextTick(() => {
          this.showMenu = true;
        });
      },
      inputFile(newFile, oldFile) {
        console.log(newFile);

        //  this.$refs.upload
        newFile.data.directory = this.data.directory + newFile.name

        if (newFile && !oldFile) {
          // Add file
        }


        if (newFile && oldFile) {
          // Update file

          // Start upload
          // if (newFile.active !== oldFile.active) {
          //   console.log('Start upload', newFile.active, newFile)

          //   // min size
          //   // if (newFile.size >= 0 && newFile.size < 100 * 1024) {
          //   //   newFile = this.$refs.upload.update(newFile, {error: 'size'})
          //   // }
          // }

          // Upload progress
          if (newFile.progress !== oldFile.progress) {
            console.log('progress', newFile.progress, newFile)
          }

          // Upload error
          if (newFile.error !== oldFile.error) {
            console.log('error', newFile.error, newFile)
          }

          // Uploaded successfully
          if (newFile.success !== oldFile.success) {
            console.log('success', newFile.success, newFile)
          }
        }

        if (!newFile && oldFile) {
          // Remove file

          // Automatically delete files on the server
          if (oldFile.success && oldFile.response.id) {
            // $.ajax({
            //   type: 'DELETE',
            //   url: '/file/delete?id=' + oldFile.response.id,
            // });
          }
        }

        // Automatic upload
        if (Boolean(newFile) !== Boolean(oldFile) || oldFile.error !== newFile.error) {
          if (!this.$refs.upload.active) {
            this.$refs.upload.active = true
          }
        }
      }
    }
  };
</script>

